import axios from '@/utils/request'

// 把所有请求语句全都放在这个文件里，清洁组件

// 获取图形验证码
export const getPic = () => {
  // 必须return 本质就是把组件的请求语句拿过来保存，组件用到时再传递回去
  return axios.get('/captcha/image')
}

// 获取短信验证码
export const getMsgCode = (captchaCode, captchaKey, mobile) => {
  return axios.post('/captcha/sendSmsCaptcha', {
    form: {
      captchaCode,
      captchaKey,
      mobile
    }
  })
}

//  请求登录，获得token
export const loginReauest = (mobile, smsCode) => {
  return axios.post('/passport/login', {
    form: {
      isParty: false,
      partyData: {},
      mobile,
      smsCode
    }
  })
}
