<template>
<div class="login">
  <!-- 头部 navbar -->
  <van-nav-bar
  title="登录"
  left-arrow
  @click-left="$router.go(-1)"
  />
  <!-- 登录页主体部分 -->
  <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input class="inp" maxlength="11" placeholder="请输入手机号码" type="text" v-model="phone">
        </div>
        <div class="form-item">
          <input class="inp" maxlength="5" placeholder="请输入图形验证码" type="text" v-model="code">
          <img v-if="codeObj.base64" :src="codeObj.base64" alt="" @click="getPicCode">
        </div>
        <div class="form-item">
          <input class="inp" placeholder="请输入短信验证码" type="text" v-model="msgCode">
          <button @click="getCode">{{second === totalSecond ? '获取验证码' :  `${second}秒后重新点击发送`}}</button>
        </div>
      </div>

      <div class="login-btn" @click="login">登录</div>
    </div>
</div>
</template>

<script>
import { getPic, getMsgCode, loginReauest } from '@/api/login'
// import { Toast } from 'vant'

export default {
  name: 'LoginPage',
  data () {
    return {
      codeObj: {}, // 请求回来的数据对象，有base64, key
      totalSecond: 60, // 总秒数
      second: 60, // 绑定秒数，点击开启定时器--
      timer: null, // 定时器变量
      phone: '', // 绑定输入手机号
      code: '', // 绑定输入图片验证码
      msgCode: '' // 绑定输入短信验证码
    }
  },
  created () {
    this.getPicCode()
  },
  destroyed () {
    // 离开页面 清除定时器
    clearInterval(this.timer)
  },
  methods: {
    // 获取图形验证码
    async getPicCode () {
      const res = await getPic()
      // console.log(res)
      this.codeObj = res.data
      // Toast('获取成功') // toast的两种导入调用方式，第二种原理是把 Toast 方法以$toast为属性名挂载到 Vue 实例的原型prototype上，所以可以在Vue组件内直接调用
      // this.$toast.success('获取成功')
    },
    // 校验要传给服务器的数据
    validFn () {
      // 正则:/^ $/ 从哪开始到哪结束 1 第一位是1 [3-9] 第二位是3-9的数字 \d{9} 后面还有9个按上个规则的数字
      if (!/^1[3-9]\d{9}$/.test(this.phone)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.code)) {
        this.$toast('图片验证码错误')
        this.getPicCode()
        return false
      }
      return true
    },
    async getCode () {
      if (!this.validFn()) { // 没通过校验就不执行
        return
      }
      // 定时器不能重复开，不然会叠加
      if (!this.timer && this.totalSecond === this.second) {
        // 发送请求
        // 如果响应的status非200，抛出一个Promise错误，await只会等待成功的Promise
        await getMsgCode(this.code, this.codeObj.key, this.phone)
        this.$toast('短信发送成功')

        this.second--
        this.timer = setInterval(() => {
          this.second--
          if (this.second < 1) {
            clearInterval(this.timer)
            this.second = this.totalSecond
            this.timer = null
          }
        }, 1000)
      }
    },
    async login () {
      if (!this.validFn()) {
        return false
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('短信验证码错误')
        return false
      }
      const res = await loginReauest(this.phone, this.msgCode)
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast.success('登录成功')
      // console.log(res.data.token)
      // 判断是否带有参数来跳转相应页面
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  }
}
</script>

<style lang="less" scoped>
  .container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
